//#region Vue
import { Vue, Component } from 'vue-property-decorator';
//#endregion

//#region Module
//#endregion

//#region Framework
import { EFormType, EFormMode } from '@/../enums';
import { ServiceFormConfig } from '@/../helpers';
import { IComponent } from '@/../models';
//#endregion

//#region Src
//#endregion

//#region Views
import { ModelsGreeting as Model } from './models';
//#endregion

//#region Components Framework
import Table from '@/../components/table/table.vue';
import Forms from '@/../components/form/forms.vue';
import Card from '@/../components/card/card.vue';
import Loading from '@/../components/loading/loading.vue';
//#endregion

//#region Components Src
//#endregion

//#region Components Views
//#endregion

//#region vue-multiselect
import Multiselect from 'vue-multiselect';
import 'vue-multiselect/dist/vue-multiselect.min.css';
//#endregion

@Component({
    components: { Table, Card, Forms, Multiselect },
})
export default class VuePageClass extends Vue {
    //#region Prop
    //#endregion

    //#region Variables
    private formItem: Model.IFormItem = {
        isClearState: false,
        inputState: null,
        isShowErrorMessage: null,
    };

    private formDataOriginal: Model.IFormData = {
        objectId: '',
        name: '',
        greeting: '',
        greetings: [],
        personTagId: [],
        personTagIds: [],
        scheduleId: null,
        scheduleIds: [],
    };
    private formData: Model.IFormData = { ...this.formDataOriginal };

    private formConfig: Model.IFormConfigs = {
        name: JSON.parse(JSON.stringify(ServiceFormConfig.getFormConfig())),
        greeting: JSON.parse(JSON.stringify(ServiceFormConfig.getFormConfig())),
    };
    private formConfigs: IComponent.IFormConfig[] = [];

    private selectOptionItem: Model.ISelectOptionItem = {
        personTagIds: [],
        scheduleIds: [],
    };

    private formButtonDisabled: Model.IFormButtonDisabled = {
        name: true,
        greeting: true,
    };

    private eFormMode = EFormMode;
    private loading = new Loading();
    //#endregion

    //#region Watch
    //#endregion

    //#region Computed
    private get formAddGreetingDisabled(): boolean {
        return !this.formData.greeting;
    }

    private get formSaveDisable(): boolean {
        this.formButtonDisabled.greeting = this.formData.greetings.length <= 0;
        return Object.values(this.formButtonDisabled).some((x) => x === true);
    }
    //#endregion

    //#region Vue Life
    private async beforeCreate(): Promise<void> {}
    private async created(): Promise<void> {}
    private async beforeMount(): Promise<void> {
        await this.initSelectionItem();
        await this.initData(FakeData);
    }
    private async mounted(): Promise<void> {}
    private async beforeDestroy(): Promise<void> {}
    private async destroyed(): Promise<void> {}
    //#endregion

    //#region Init
    //#region Init form
    private async initData(rowData: Model.ITableData): Promise<void> {
        this.initFormConfig();
        await this.formConfigSet(rowData);
    }

    private initFormConfig(): void {
        // name
        this.formConfig.name.id = 'name';
        this.formConfig.name.label = this.$i18n.Common_Name;
        this.formConfig.name.placeholder = this.$i18n.Common_Name;
        this.formConfig.name.formType = EFormType.text;
        this.formConfig.name.required = true;

        // greeting
        this.formConfig.greeting.id = 'greeting';
        this.formConfig.greeting.formType = EFormType.slot;

        this.formConfigs = [this.formConfig.name, this.formConfig.greeting];
    }

    private initFormData(): void {
        this.initFormConfig();
    }
    //#endregion

    //#region Init select option
    private async initSchedule(): Promise<void> {
        this.selectOptionItem.scheduleIds = [
            { objectId: '61ca5dc335ada30fa07604b4', name: 'All Day' },
            { objectId: '61ca5e7635ada30fa07604b6', name: 'Work' },
            { objectId: '61d537bd2d4ff144b01100ce', name: 'Holiday' },
        ];
    }

    private async initPersonTag(): Promise<void> {
        this.selectOptionItem.personTagIds = [
            { objectId: 'isAllowEmptyTag', name: this.$i18n.Greeting_IsAllowEmptyTag },
            { objectId: '61c96e5b82bf3d53986cfa83', name: 'Administrator' },
            { objectId: '61c96e5c82bf3d53986cfa86', name: 'Employee' },
            { objectId: '61c96e5c82bf3d53986cfa88', name: 'Blacklist' },
            { objectId: '61c96e5c82bf3d53986cfa8a', name: 'VIP' },
            { objectId: '61c96e5c82bf3d53986cfa8c', name: 'Watchlist' },
        ];
    }

    private async initSelectionItem(): Promise<void> {
        await this.initPersonTag();
        await this.initSchedule();
    }
    //#endregion
    //#endregion

    //#region View Event
    //#region Event form
    private formConfigValues(value: any, disabledButton: boolean, id: string): void {
        if (this.formData.hasOwnProperty(id)) this.formData[id] = value;
        if (this.formButtonDisabled.hasOwnProperty(id)) this.formButtonDisabled[id] = disabledButton;
    }
    //#endregion

    //#region Event form person tag and greeting
    private formInputGreeting(): void {
        this.formItem.inputState = !!this.formData.greeting;
        this.formItem.isShowErrorMessage = !this.formData.greeting;
    }

    private formAddGreeting(): void {
        const emptyValue: IComponent.ISelectionBase = { objectId: '', name: '' };
        const scheduleId: IComponent.ISelectionBase = this.formData.scheduleId ?? emptyValue;

        this.formData.personTagId = this.formData.personTagId.length > 0 ? this.formData.personTagId : [emptyValue];

        this.formData.greetings.push(this.formData.greeting);
        this.formData.personTagIds.push(this.formData.personTagId);
        this.formData.scheduleIds.push(scheduleId);

        this.formData.greeting = '';
        this.formData.personTagId = [];
        this.formData.scheduleId = null;

        this.formItem.inputState = null;
        this.formItem.isShowErrorMessage = null;
    }

    private formRemoveGreeting(index: number): void {
        this.formData.greetings.splice(index, 1);
        this.formData.personTagIds.splice(index, 1);
        this.formData.scheduleIds.splice(index, 1);
    }
    //#endregion

    //#region Event server api (save, delete, test, sync...)
    //#endregion
    //#endregion

    //#region Other Function
    //#region Form
    private formTitle(title: string = ''): string {
        return title;
    }

    private formDataClear(): void {
        this.formData = JSON.parse(JSON.stringify({ ...this.formDataOriginal }));

        this.formItem.isClearState = false;
        this.formItem.inputState = null;
        this.formItem.isShowErrorMessage = null;

        this.formData.greetings = [];
        this.formData.personTagIds = [];
        this.formData.scheduleIds = [];
    }

    private async formConfigSet(data: Model.ITableData = undefined): Promise<void> {
        if (data) await this.formDataSet(data);
    }

    private async formDataSet(data: any): Promise<void> {
        this.formData.objectId = data.objectId ?? '';
        this.formData.name = data.name ?? '';

        let messages = data.messages ?? [];

        this.formData.greetings = [];
        this.formData.personTagIds = [];
        this.formData.scheduleIds = [];

        const emptyValue: IComponent.ISelectionBase = { objectId: '', name: '' };

        (messages || []).forEach((item) => {
            this.formData.greetings.push(item.content);

            let scheduleId: IComponent.ISelectionBase = item.schedules?.length > 0 ? item.schedules[0] : emptyValue;
            let personTagId: IComponent.ISelectionBase[] = [];

            if (!!item.personTags) {
                item.personTags.forEach((x) => personTagId.push(x));
                if (item.isAllowEmptyTag) {
                    personTagId.push({ objectId: 'isAllowEmptyTag', name: this.$i18n.Greeting_IsAllowEmptyTag });
                }
            } else {
                if (item.isAllowEmptyTag) {
                    personTagId.push({ objectId: 'isAllowEmptyTag', name: this.$i18n.Greeting_IsAllowEmptyTag });
                } else {
                    personTagId = [emptyValue];
                }
            }

            this.formData.personTagIds.push(personTagId);
            this.formData.scheduleIds.push(scheduleId);
        });

        this.formItem.isClearState = false;
    }
    //#endregion

    //#region Form person tag and greeting
    private formShowGreetingDisabled(greeting: IComponent.ISelectionBase[]): boolean {
        return !(greeting.length === 1 && greeting[0].objectId === '');
    }
    //#endregion

    //#region Server response
    //#endregion

    //#region Resolve text
    //#endregion
    //#endregion
}
//#region Fake Data
let FakeData = {
    objectId: '61ca5f0c35ada30fa07604b8',
    name: 'a-z',
    messages: [
        { content: 'a', isAllowEmptyTag: false },
        {
            content: 'b',
            isAllowEmptyTag: true,
            schedules: [{ objectId: '61ca5dc335ada30fa07604b4', name: 'All Day' }],
        },
        {
            content: 'c',
            personTags: [{ objectId: '61c96e5c82bf3d53986cfa86', name: 'Employee' }],
            isAllowEmptyTag: false,
            schedules: [{ objectId: '61ca5e7635ada30fa07604b6', name: 'Work' }],
        },
        {
            content: 'd',
            personTags: [{ objectId: '61c96e5b82bf3d53986cfa83', name: 'Administrator' }],
            isAllowEmptyTag: false,
        },
        {
            content: 'e',
            isAllowEmptyTag: false,
            schedules: [{ objectId: '61ca5dc335ada30fa07604b4', name: 'All Day' }],
        },
    ],
};
//#endregion
